.base {
    height: 9em !important;
    left: 50%;
    margin: -7.5em;
    padding: 3em;
    position: absolute;
    top: 50%;
    width: 9em;
    -webkit-transform: rotateX(45deg) rotateZ(45deg);
            transform: rotateX(45deg) rotateZ(45deg);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.cube,
.cube:after,
.cube:before {
    content: '';
    float: left;
    height: 3em;
    position: absolute;
    width: 3em;
}
/* Top */
.cube {
    background-color: #05afd1;
    position: relative;
    -webkit-transform: translateZ(3em) !important;
            transform: translateZ(3em) !important;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: .25s;
    transition: .25s;
    box-shadow: 13em 13em 1.5em rgba(255, 255, 255, 0.1);
    -webkit-animation: anim 1s infinite;
            animation: anim 1s infinite;
}

.cube:after {
    background-color: #049dbc;
    -webkit-transform: rotateX(-90deg) translateY(3em);
            transform: rotateX(-90deg) translateY(3em);
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
}

.cube:before {
    background-color: #048ca7;
    -webkit-transform: rotateY(90deg) translateX(3em);
            transform: rotateY(90deg) translateX(3em);
    -webkit-transform-origin: 100% 0;
            transform-origin: 100% 0;
}

.cube:nth-child(1) {
    -webkit-animation-delay: 0.05s;
            animation-delay: 0.05s;
}

.cube:nth-child(2) {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}

.cube:nth-child(3) {
    -webkit-animation-delay: 0.15s;
            animation-delay: 0.15s;
}

.cube:nth-child(4) {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}

.cube:nth-child(5) {
    -webkit-animation-delay: 0.25s;
            animation-delay: 0.25s;
}

.cube:nth-child(6) {
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
}

.cube:nth-child(7) {
    -webkit-animation-delay: 0.35s;
            animation-delay: 0.35s;
}

.cube:nth-child(8) {
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}

.cube:nth-child(9) {
    -webkit-animation-delay: 0.45s;
            animation-delay: 0.45s;
}

@-webkit-keyframes anim {
    50% {
        -webkit-transform: translateZ(0.5em);
                transform: translateZ(0.5em);
    }
}

@keyframes anim {
    50% {
        -webkit-transform: translateZ(0.5em);
                transform: translateZ(0.5em);
    }
}

.header {
